import { JsonEntityFn } from "./jsonEntityFn";
/**
 * Set the property as WriteOnly.
 *
 * @returns {Function}
 * @decorator
 * @validation
 * @property
 * @parameter
 * @schema
 */
export function WriteOnly(writeOnly = true) {
    return JsonEntityFn((store) => {
        store.itemSchema.writeOnly(writeOnly);
    });
}
