import { JsonEntityFn } from "./jsonEntityFn";
/**
 * Allow to forward group on specific property.
 *
 * @decorator
 * @validation
 * @swagger
 * @schema
 * @input
 */
export function ForwardGroups(bool = true) {
    return JsonEntityFn((entity) => {
        entity.schema.$forwardGroups = bool;
    });
}
