import { JsonEntityFn } from "./jsonEntityFn";
/**
 * Add a examples metadata on the decorated element.
 *
 * @decorator
 * @swagger
 * @schema
 * @input
 * @methodDecorator
 * @classDecorator
 */
export function Examples(examples) {
    return JsonEntityFn((store) => {
        store.parameter.examples(examples);
    });
}
