export function alterOneOf(obj, schema, options) {
    if (obj.oneOf && options.groups !== false) {
        obj = { ...obj, oneOf: schema.$hooks.alter("oneOf", obj.oneOf, [options.groups]) };
    }
    if ((obj.oneOf || obj.allOf || obj.anyOf) && !(obj.items || obj.properties)) {
        delete obj.type;
    }
    return obj;
}
