import { JsonEntityFn } from "./jsonEntityFn";
/**
 * Set the property as readOnly.
 *
 * @returns {Function}
 * @decorator
 * @validation
 * @property
 * @parameter
 * @schema
 */
export function ReadOnly(readOnly = true) {
    return JsonEntityFn((store) => {
        store.itemSchema.readOnly(readOnly);
    });
}
