import { cleanObject } from "@tsed/core";
/**
 * @ignore
 * @param info
 */
export function mapOpenSpecInfo(info) {
    const { title, description, version, termsOfService, contact, license } = info;
    return cleanObject({
        version,
        title,
        description,
        termsOfService,
        contact,
        license
    });
}
