import { JsonEntityFn } from "./jsonEntityFn";
/**
 * Add an example metadata on the decorated element.
 *
 * @decorator
 * @swagger
 * @schema
 * @input
 * @methodDecorator
 * @classDecorator
 */
export function Example(...examples) {
    return JsonEntityFn((store) => {
        store.schema.examples(examples);
    });
}
